@media screen and (max-width: 480px) {
    
    .outer-box {
        /* border: 1rem solid red; */
        display:flex;
        flex-direction: row;
        align-items:flex-start;
        width:100%;
        
    }
    .button-box {
    /* border: 1px solid blue; */
    display: flex;
    flex-direction:row;
    }


    .rand-button {
    order:1;
    border: dotted black;
    height: 25vw;
    width: 25vw;
    border-radius: 30%;
    padding: 5vw;
    font-size:3.5vw;
    } 

    .checkbox {
    order: 2;
    }

    .character-info-box {
    /* outline:1rem solid fuchsia; */
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    margin-top:-40%;
    margin-left:10%;
    width:30vw;
    z-index: -1;
    }

    .mand-card {
    font-size: 25vw; 
    margin-top:-20%;
    }

    .pinyin-card {
    font-size: 10vw;
    margin-top:-75%;
    } 

    .english-card {
    font-size:5vw;
    margin-top:-25%;
    }
  }
 

  @media screen and (max-width: 2000px) and (min-width:480px){
           
    .outer-box {
        /* border: 1rem solid red; */
        display:flex;
        flex-direction: row;
        align-items:flex-start;
        width:100%;
        
    }
    .button-box {
    /* border: 1px solid blue; */
    display: flex;
    flex-direction:row;
    }


    .rand-button {
    order:1;
    border: dotted black;
    height: 10vw;
    width: 10vw;
    border-radius: 30%;
    padding: 2.5vw;
    font-size:1.5vw;
    } 

    .checkbox {
    order: 2;
    }

    .character-info-box {
    /* outline:1rem solid fuchsia; */
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    margin-top:-25%;
    margin-left:50%;
    width:10vw;
    z-index: -1;
    }

    .mand-card {
    font-size: 15vw;
    margin-top:-20%;
    }

    .pinyin-card {
    font-size: 5vw;
    margin-top:-115%;
    } 

    .english-card {
    font-size:2.5vw;
    margin-top:-50%;
    }

    }