@media screen and (max-width:480px){

        .page {
            margin-top:-2rem;
        }
        .Parent{
            border:1px solid black;
        } 
        
        .Library{
            display:flex;
            /* border: 1px solid orange; */
            flex-direction:column;
            width:100%;
            margin-left:5vw;
        }

        .LessonSelect{
            margin-left: 1.5vw;
        }

        h2 {
            display:flex;
            justify-content:center;
            font-size:7vw;
        }
    }

@media screen and (max-width:2000px) and (min-width:480px){

    .page {
        margin-top:-2rem;
    }
    
    .Library{
        display:flex;
        /* border: 1px solid orange; */
        flex-direction:column;
        width:100%;
        margin-left:5vw;
    }

    .LessonSelect{
        margin-left: 1.5vw;
    }

    h2 {
        display:flex;
        justify-content:center;
        font-size:7vw;
    }
}