@media screen and (max-width: 500px){

.LessonDisplay {
    font-size: 5vw;
    color:darkcyan;
    text-align: center;
    border: solid lightblue;
    height: 20vw;
    width: 20vw;
    border-radius: 30%;
    padding: 5vw;
    display: flex;
    flex-direction: column; 
    justify-content: center;
  }
}

@media screen and (max-width:2000px) and (min-width:480px){
  .LessonDisplay {
    font-size: 2.5vw;
    color:darkcyan;
    text-align: center;
    border: solid lightblue;
    height: 10vw;
    width: 10vw;
    border-radius: 30%;
    padding: 3.5vw;
    display: flex;
    flex-direction: column; 
    justify-content: center;
}
}